import React, {  } from 'react'
import { useDispatch } from 'react-redux'

import { toggleTimer } from '../actions'
import { removeTimer} from '../actions'
import { resetTimer } from '../actions'

import { formatTime } from '../utils';

import './TimerView.css'

export default function TimerView(props) {

  const { index, timer } = props
  const dispatch = useDispatch()

  return (
    <div className="TimerView">
      <h3>{timer.name}</h3>
      <p>({timer.category})</p>
      <h1>{formatTime(timer.time)}</h1>
      <button className={timer.isRunning? "btn btn-danger m-2":"btn btn-success m-2"}
        onClick={() => dispatch(toggleTimer(index))}
      >
        {timer.isRunning ? "Stop" : "Start"}
      </button>
      <button id ="delete" className="btn btn-danger m-2" 
        onClick={() => dispatch(removeTimer(index))}
      >
        X
        </button>
        <button id="reset" className="btn btn-warning" onClick={() => dispatch(resetTimer(index))}><div><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-bootstrap-reboot" viewBox="0 0 16 16">
  <path d="M1.161 8a6.84 6.84 0 1 0 6.842-6.84.58.58 0 1 1 0-1.16 8 8 0 1 1-6.556 3.412l-.663-.577a.58.58 0 0 1 .227-.997l2.52-.69a.58.58 0 0 1 .728.633l-.332 2.592a.58.58 0 0 1-.956.364l-.643-.56A6.812 6.812 0 0 0 1.16 8z"/>
  <path d="M6.641 11.671V8.843h1.57l1.498 2.828h1.314L9.377 8.665c.897-.3 1.427-1.106 1.427-2.1 0-1.37-.943-2.246-2.456-2.246H5.5v7.352h1.141zm0-3.75V5.277h1.57c.881 0 1.416.499 1.416 1.32 0 .84-.504 1.324-1.386 1.324h-1.6z"/>
</svg></div></button>
    </div>
  )
}